import { Injectable, EventEmitter } from '@angular/core';

@Injectable()
export class GdaSidenavService {
  toggle: EventEmitter<number>;
  widthContainer: EventEmitter<number>;
  opened: boolean;
  directionsEmit: EventEmitter<'left' | 'right'>;
  directions: 'left' | 'right';

  constructor() {
    this.toggle = new EventEmitter();
    this.widthContainer = new EventEmitter();
    this.opened = false;
    this.directionsEmit = new EventEmitter();
    this.directions = 'left';
   }
}
